import React from 'react';
import { slide as Menu } from 'react-burger-menu';
import ScrollTo from '../components/ScrollTo';

export default class MainMenu extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      menuOpen: false,
      isTop: true,
      vpWidth: null
    };

    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
    document.addEventListener('scroll', () => {
      const isTop = window.scrollY < 1;
      if (isTop !== this.state.isTop) {
        this.setState({ isTop });
      }
    });
  }

  updateWindowDimensions() {
    this.setState({ vpWidth: window.innerWidth });
  }

  componentWillUnmount() {
    this.state.vpWidth;
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  handleStateChange(state) {
    this.setState({ menuOpen: state.isOpen });
  }

  toggleMenu() {
    this.setState({ menuOpen: !this.state.menuOpen });
  }

  closeMenu() {
    this.setState({ menuOpen: false });
  }

  renderMenu() {
    return this.state.vpWidth > 1024
      ? this.renderDesktopMenu()
      : this.renderMobileMenu();
  }

  renderDesktopMenu() {
    return (
      <React.Fragment>
        <nav>
          <ul className="navigation">
            <li>
              <ScrollTo onClick={() => this.closeMenu()} href="about">
                About
              </ScrollTo>
            </li>
            <li>
              <ScrollTo onClick={() => this.closeMenu()} href="team">
                Team
              </ScrollTo>
            </li>
            <li>
              <ScrollTo onClick={() => this.closeMenu()} href="join-us">
                Join us
              </ScrollTo>
            </li>
          </ul>
        </nav>

        <ul className="social-links">
          <li>
            <a href="linkedin">linkedin</a>
          </li>
          <li>
            <a href="twitter">twitter</a>
          </li>
          <li>
            <a href="github">github</a>
          </li>
          <li>
            <a href="NATSio">NATSio</a>
          </li>
        </ul>

        <style jsx>{`
          .navigation {
            display: inline-block;
            list-style: none;
          }
          .navigation li {
            display: inline-block;
            margin: 0 1rem;
          }
          .navigation li:first-child {
            margin-left: 0;
          }
          .navigation li:last-child {
            margin-right: 0;
          }
          .social-links {
            display: inline-block;
          }
        `}</style>
      </React.Fragment>
    );
  }

  renderMobileMenu() {
    return (
      <Menu
        right
        isOpen={this.state.menuOpen}
        onStateChange={state => this.handleStateChange(state)}
      >
        <nav>
          <ScrollTo onClick={() => this.closeMenu()} href="about">
            About
          </ScrollTo>
          <ScrollTo onClick={() => this.closeMenu()} href="team">
            Team
          </ScrollTo>
          <ScrollTo onClick={() => this.closeMenu()} href="join-us">
            Join us
          </ScrollTo>
        </nav>
        <ul className="social-links">
          <li>
            <a href="linkedin">
              <svg
                width="25px"
                height="25px"
                viewBox="0 0 25 25"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                xmlnsXlink="http://www.w3.org/1999/xlink"
              >
                <g stroke="none" strokeWidth="1" fill="#FFF" fillRule="evenodd">
                  <path d="M0.38275811,10.2684822 L5.45430307,10.2684822 L5.45430307,27 L0.38275811,27 L0.38275811,10.2684822 Z M2.96637535,2 C4.59309732,2 5.9327507,3.36186765 5.9327507,5.01556408 C5.9327507,6.66926052 4.59309732,8.03112817 2.96637535,8.03112817 C1.33965338,8.03112817 -1.77635684e-13,6.66926052 -1.77635684e-13,5.01556408 C-1.77635684e-13,3.36186765 1.33965338,2 2.96637535,2 Z M8.707747,10.2684822 L13.5879129,10.2684822 L13.5879129,12.5058362 L13.6836024,12.5058362 C14.3534291,11.1439685 16.0758406,9.78210086 18.5637683,9.78210086 C23.7310028,9.78210086 24.6878981,13.2840463 24.6878981,17.7587543 L24.6878981,26.9027228 L19.6163531,26.9027228 L19.6163531,18.7315169 C19.6163531,16.7859916 19.6163531,14.2568089 16.9370464,14.2568089 C14.2577396,14.2568089 13.8749815,16.3968866 13.8749815,18.5369643 L13.8749815,26.8054465 L8.80343653,26.8054465 L8.80343653,10.2684822 L8.707747,10.2684822 Z" />
                </g>
              </svg>
            </a>
          </li>
          <li>
            <a href="twitter">
              <svg
                width="25px"
                height="22px"
                viewBox="0 0 25 22"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                xmlnsXlink="http://www.w3.org/1999/xlink"
              >
                <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                  <g
                    transform="translate(-1526.000000, -41.000000)"
                    fill="#FFFFFF"
                    fillRule="nonzero"
                  >
                    <g id="nav-bar">
                      <g transform="translate(1451.000000, 38.000000)">
                        <path
                          d="M99.6878981,5.49085758 C98.7792448,5.91354575 97.8042729,6.20020802 96.7798848,6.32818754 C97.8258547,5.67063028 98.6265184,4.62765222 99.0059946,3.38868169 C98.0248508,3.99762198 96.941849,4.43979081 95.7878896,4.67948773 C94.8638265,3.64459839 93.5494358,3 92.0915566,3 C89.2946391,3 87.0268583,5.38074946 87.0268583,8.31538514 C87.0268583,8.73159386 87.0715951,9.1381046 87.1580029,9.52682864 C82.9495162,9.30491841 79.2177245,7.18817437 76.7200877,3.97170422 C76.2834904,4.75558939 76.0351185,5.66897866 76.0351185,6.64398612 C76.0351185,8.4886784 76.9298949,10.1163306 78.2874894,11.0685965 C77.4574987,11.0394602 76.6768838,10.7997633 75.9934878,10.4013413 L75.9934878,10.4677449 C75.9934878,13.0428352 77.7398367,15.1919765 80.0554202,15.6811107 C79.6311668,15.8009591 79.1837988,15.8673628 78.7209806,15.8673628 C78.3939461,15.8673628 78.0776823,15.8333563 77.7675905,15.7685619 C78.4124217,17.8820874 80.2822103,19.4190275 82.4975494,19.461165 C80.7650774,20.8863877 78.5805982,21.7334156 76.207934,21.7334156 C75.7991308,21.7334156 75.3964592,21.7074979 75,21.6605325 C77.2415599,23.1715972 79.9027341,24.0526316 82.7629044,24.0526316 C92.0792933,24.0526316 97.1717857,15.9515955 97.1717857,8.92597706 L97.1548027,8.23767422 C98.1498234,7.49262334 99.0106337,6.55653486 99.6878981,5.49085758 Z"
                          id="Shape"
                        />
                      </g>
                    </g>
                  </g>
                </g>
              </svg>
            </a>
          </li>
          <li>
            <a href="github">github</a>
          </li>
          <li>
            <a href="NATSio">NATSio</a>
          </li>
        </ul>
        <style jsx global>{`
          /* Position and sizing of burger button */
          .bm-burger-button {
            position: relative;
            width: 1.4rem;
            height: 14px;
          }

          /* Color/shape of burger icon bars */
          .bm-burger-bars {
            position: absolute;
            height: 2px !important;
            border-radius: 4px;
            left: 0;
            right: 0;
            background-color: var(--color-font);
          }

          .bm-burger-bars:nth-child(1) {
            top: 0 !important;
          }
          .bm-burger-bars:nth-child(2) {
            top: 50% !important;
            transform: translateY(-50%);
          }
          .bm-burger-bars:nth-child(3) {
            top: 100% !important;
            transform: translateY(-100%);
          }

          .bm-cross-button {
            right: 4vw !important;
            top: 4vw !important;
          }

          /* Color/shape of close button cross */
          .bm-cross {
            cursor: pointer;
            background: var(--color-font);
            height: 20px !important;
          }

          /* General sidebar styles */
          .bm-menu {
            background: var(--color-quaternary) !important;
            padding: 4em 1.5em 0;
            font-size: 1.15em;
            transition: all 400ms ease;
          }

          .bm-menu button {
            width: 80%;
            margin: 1rem 0;
          }

          .bm-menu button:first-of-type {
            margin-top: 30%;
          }

          .bm-menu-wrap {
            background: var(--color-quaternary) !important;
            top: 0;
          }

          /* Wrapper for item list */
          .bm-item-list {
            padding: 0.8em;
          }

          /* Styling of overlay */
          .bm-overlay {
            top: 0;
            left: 0;
            width: 100vw !important;
            height: 100vh !important;
            z-index: 1001 !important;
            background: var(--color-quaternary) !important;
          }
        `}</style>
      </Menu>
    );
  }

  render() {
    return (
      <React.Fragment>
        {this.renderMenu()}
        <style jsx global>{`
          .social-links {
            list-style-type: none;
          }
          .social-links li {
            display: inline-block;
          }
        `}</style>
      </React.Fragment>
    );
  }
}
